
import { defineComponent } from 'vue';
import { mapActions, mapGetters } from 'vuex';
import { ActionTypes } from '@/store/modules/auth/actions';
import { ActionTypes as OrderActionTypes } from '@/store/modules/order/actions'

export default defineComponent({
  name: 'Header',
  data () {
    return{
      orderSearch: '',
      showResult: false,
      orderDate: ''
    }
  },
  computed: {
    ...mapGetters({
      userdata: 'getUser',
      orders: 'getListOfOrders'
    }),
    admin(){
      const allowedRoles = ['ADMIN','SUPER_ADMIN'];
      if(this.userdata != null && allowedRoles.includes(this.userdata.user_type)){
        return true;
      }
      return false;
    },
    superadmin(){
      const allowedRoles = ['SUPER_ADMIN'];
      if(this.userdata != null && allowedRoles.includes(this.userdata.user_type)){
        return true;
      }
      return false;
    }
  },
  methods: {
    clear: function () {
      this.orderSearch = '';
      this.showResult = false;
      this.orderDate = '';
    },

    searchOrder: function () {
      this.$router.push({
        name: 'OrdersList',
        query: {
          invoiceId: this.orderSearch,
          date: this.orderDate
        }
      });
      this.clear();
    },

    orderDetails: function (id: number) {
      this.$router.push({
        name: 'OrderDetails',
        params: {
          orderId: id
        }
      });
      this.clear();
    },

    onlyDate: function (value: string) {
      const givenDate = new Date(value)
      return givenDate.toDateString()
    },

    ...mapActions({
      logoutUser: ActionTypes.LOGOUT_USER,
      getuserdate: ActionTypes.USER_DATA,
      fetchOrders: OrderActionTypes.FETCH_ORDERS,
    }),

    searchTyped() {
      if (this.orderSearch == ''){
        this.showResult = false;
        return
      }
      this.fetchOrders({
        id__contains: this.orderSearch
      });
      this.showResult = true;
    },

    logout() {
      this.logoutUser();
    }
  },
  created() {
    this.getuserdate();
  },
});
